

















import Vue from 'vue'
import Component from 'vue-class-component'

@Component({
  props: {
    searchPlaceholder: String
  }
})
export default class extends Vue {
  keyValue: string = ''

  created() {}

  mounted() {}

  doSearch() {
    this.$emit('search', this.keyValue)
  }
  doEnterSearch(e) {
    this.$emit('search', this.keyValue)
  }
  backspaceSearch() {
    this.$emit('search', this.keyValue)
  }
}



